@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.2/css/all.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.2/css/all.min.css);
@import url(https://fonts.googleapis.com/css2?family=Nunito&family=Open+Sans&family=Quicksand&family=Saira:ital,wght@1,300;1,400&display=swap);
:root {
  --primary-bg-color: #ffffff;
  --secondary-bg-color: #e4f7ff;
  --terinary-bg-color: #ffffff;

  --primary-red-accent: #ffd9dd;
  --secondary-red-accent: #c12e3e;
  --primary-blue-accent: #a4d7f4;
  --secondary-blue-accent: #1789c9;
  --terinary-blue-accent: #1373ab;

  --primary-lggreen-accent: #b3ffd9;
  --secondary-lggreen-accent: #00ff7f;

  --primary-drgreen-accent: #4eff10;
  --secondary-drgreen-accent: #84cc16;
  --primary-saffron-accent: #ffccb3;
  --secondary-saffron-accent: #ff9966;

  --primary-color: #000000;
  --secondary-color: #1789c9;
  --terinary-color: #ffffff;

  /* --secondary-drgreen-accent: #add626; */
}
.line {
  width: 100%;
  height: 2px;
  border-radius: 5px;
  background-color: #1373ab;
  margin-bottom: 5px;
}
.Heading {
  font-size: 22px;
  color: #1373ab;
  color: var(--terinary-blue-accent);
  color: #1373ab;
  padding-top: 10px;
}
.page {
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-size: 16px;
  height: 92.5%;
  padding: 30px;
}
.work {
  border: 1px solid #1789c9;
  border: 1px solid var(--secondary-blue-accent);
  height: 98%;
  background: white;
  padding: 10px;
}
.detail {
  display: flex;
  justify-content: space-between;
}
.sec1 {
  width: 60%;
}
.Name {
  font-size: 28px;
}
.Title {
  font-size: 24px;
}

.sec2 {
  display: flex;
  flex-flow: column wrap;
  grid-gap: 3px;
  gap: 3px;
  width: 38%;
  font-size: 14px;
}
.sec2 > div {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.icon1,
.icon2,
.icon3,
.icon4 {
  font-size: 16px;
  padding-right: 4px;
}
.icon1::before,
.icon2::before,
.icon3::before,
.icon4::before {
  font-size: 12px;
  color: #1373ab;
  color: var(--terinary-blue-accent);
}
.icon1::before {
  display: inline-block;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font: var(--fa-font-solid);
  content: "\F095     -";
}

.icon2::before {
  display: inline-block;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font: var(--fa-font-solid);
  content: "\F0E0    -";
}
.icon3::before {
  display: inline-block;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font: var(--fa-font-brands);
  content: "\F08C    -";
}

.icon4::before {
  display: inline-block;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font: var(--fa-font-brands);
  content: "\F09B    -";
}

/**** EXP  ****/
.sub-sec {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  font-size: 18px;
}
.sub-about {
  padding: 1px 10px;
  font-size: 16px;
}
.date {
  color: grey;
}
.sub-name {
  font-size: 18px;
}
/****** INTRO *****/
.intro {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.intro > ul {
  max-width: 450px;
  font-size: 18px;
}

.intro > ul > li {
  padding: 5px;
}
.clear-demo {
  font-weight: 600;
  border: 1px solid #c12e3e;
  border: 1px solid var(--secondary-red-accent);
  background-color: #ffd9dd;
  background-color: var(--primary-red-accent);
  color: #c12e3e;
  color: var(--secondary-red-accent);
  border-radius: 5px;
  cursor: text;
}

.nxt-btn-demo {
  font-weight: 600;
  font-size: 20px;
  color: #1789c9;
  color: var(--secondary-color);
  background-color: #e4f7ff;
  background-color: var(--secondary-bg-color);
  border-radius: 50%;
  border: 1px solid #1789c9;
  border: 1px solid var(--secondary-blue-accent);
}

.pdf-btn-demo {
  font-weight: 600;
  background-color: #1789c9;
  background-color: var(--secondary-blue-accent);
  color: #ffffff;
  color: var(--terinary-color);
  border-radius: 5px;
  border: 1px solid #1789c9;
  border: 1px solid var(--secondary-blue-accent);
}

.reset-btn-demo {
  font-weight: 600;
  background-color: #c12e3e;
  background-color: var(--secondary-red-accent);
  color: #ffd9dd;
  color: var(--primary-red-accent);
  border-radius: 5px;
  border: 1px solid #c12e3e;
  border: 1px solid var(--secondary-red-accent);
}

.demo-btn-demo {
  font-weight: 600;
  background-color: #84cc16;
  background-color: var(--secondary-drgreen-accent);
  color: #4eff10;
  color: var(--primary-drgreen-accent);
  border: 1px solid #84cc16;
  border: 1px solid var(--secondary-drgreen-accent);
  border-radius: 5px;
}

.dlt-btn-demo {
  font-size: 22px;

  color: #c12e3e;

  color: var(--secondary-red-accent);
  border: none;
}
.add-btn-demo {
  font-size: 22px;
  color: #1789c9;
  color: var(--secondary-color);
  background-color: #e4f7ff;
  background-color: var(--secondary-bg-color);
  border-radius: 50%;
  cursor: pointer;
}

